.game-field {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  border-radius: $_5px;
  width: 80%;
  margin: auto;
}

.tile-wrap {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  width: 40vw;
  height: 40vw;
  background-color: #a8c577;
  border-radius: $_5px;
  padding-top: $_10px;
  padding-bottom: $_10px;
}

.square{
  display: flex;
  width: 22%;
  height: 22%;
  margin: 1%;
  border-radius: 3px;
  background: rgba(131, 150, 100, 0.4);
  justify-content: center;
  align-items: center;
  font-size: $_42px;
  box-shadow: $_4px $_4px $_6px #63704e;
  position: relative;
}

.info-wrap {
  margin-bottom: $_50px;
}

.title-wrap{
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: $_10px;
}

h1 {
  font-size: $_78px;
  color: #90d620;
  text-shadow: 3px 3px 4px #4d5a37;
}

.score-wrap {
  display: flex;
  flex-direction: column;
  background: rgba(150, 179, 103, 0.6);
  padding: $_20px $_30px;
  font-size: $_30px;
  letter-spacing: $_3px;
  font-weight: 600;
  border-radius: $_3px;
  color: #fff;
  text-align: center;
  margin-left: $_20px;
}

#score-label {
  font-size: $_30px;
  color: #ebd1d1;
}

#score-element {
  font-size: $_36px;
  color: #ebd1d1;
}

.btn-wrap {
  display: flex;
  align-items: center;
  justify-content: center;
   margin-left: $_20px;
  margin-top: $_10px;
  cursor: pointer;
}

.new-game-btn {
@include btnStyle;
}

.raitings-btn {
  display: block;
margin: $_15px 0 0 $_30px;
  padding: $_10px $_15px;
  @include btnStyle;
  color:#d64828;
}

.tile{
  width: 22%;
  height: 22%;
  border-radius: $_4px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
  font-size: 60px;
  text-align: center;
  font-weight: 600;
  position: absolute;
  transition: top 0.25s, left 0.25s, width 1s, height 1s;
}

.row1 {
  top: 75%;
}
.row2 {
  top: 51%;
}
.row3 {
  top: 27%;
}
.row4 {
  top: 3%;
}

.column1 {
  left: 3%;
}
.column2 {
  left: 27%;
}
.column3 {
  left: 51%;
}
.column4 {
  left: 75%;
}

.value2 {
  background-color: #a0e6af;
  color: #899483;
}

.value4 {
  background-color: #8ad69b;
  color: #727371;
}

.value8 {
  background-color: #76c788;
  color: #ffffff;
}

.value16{
  background-color: #62ba75;
  color: #ffffff;
}

.value32{
  background-color: #4ca65f;
  color: #ffffff;
}

.value64{
  background-color: #3a884b;
  color: #ffffff;
}

.value128{
  background-color: #2c803e;
  color: #ffffff;
}

.value256{
  background-color: #1b6b2c;
  color: #ffffff;
}

.value512{
  background-color: #0f6020;
  color: #ffffff;
}

.value1024{
  background-color: #075017;
  color: #ffffff;
}

.value2048{
  background-color: #02390e;
  color: #ffffff;
}

@keyframes blink {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.7);
  }
  100% {
    transform: scale(1);
  }
}

.merged {
  animation: blink 0.25s;
}
